import React, { useEffect } from 'react';
import { Form, Input, Modal } from 'antd';
import { useTasksModal, useTasksQueryKey } from './util';
import { useForm } from 'antd/lib/form/Form';
import { useEditTask } from 'utils/task';
import UserSelect from 'components/user-select';
import TaskTypeSelect from 'components/task-type-select';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 18}
}

export const TaskModal = () => {
  const { close, editingTask, editingTaskId } = useTasksModal()
  const [form] = useForm()
  const { mutateAsync: updateTask, isLoading: editLoading } = useEditTask(useTasksQueryKey())

  const cancelModal = () => {
    form.resetFields()
    close()
  }
  const onUpdateTask = async () => {
    await updateTask({...editingTask, ...form.getFieldsValue()})
    close()
  }

  // 数据回填
  useEffect(() => {
    form.setFieldsValue(editingTask)
  }, [form, editingTask])

  return <Modal okText={'确认'} cancelText={'取消'} onCancel={cancelModal} onOk={onUpdateTask} confirmLoading={editLoading} 
    visible={!!editingTaskId} forceRender={true} title={'编辑任务'}>
    <Form {...layout} initialValues={editingTask} form={form}>
      <Form.Item label={'任务名'} name={'name'} rules={[{ required: true, message: '请输入任务名' }]}>
        <Input placeholder={'请输入任务名'} />
      </Form.Item>
      <Form.Item label={'经办人'} name={'processorId'}>
        <UserSelect defaultValueName={'经办人'}></UserSelect>
      </Form.Item>
      <Form.Item label={'类型'} name={'typeId'} rules={[{ required: true, message: '请选择类型' }]}>
        <TaskTypeSelect defaultValueName={'类型'}></TaskTypeSelect>
      </Form.Item>
    </Form>
  </Modal>
}