import React, { useMemo } from "react"; // useEffect
import { SearchPanel } from './search-panel';
import { List } from "./p-list";
import styled from "@emotion/styled"
import { useDebounce } from "utils"; // cleanObject
import { Button } from "antd";
import { useProjects } from "utils/project";
import { useUrlQueryParam } from "utils/url";
import { ErrorBox, Row } from 'components/lib';
import { useProjectModal } from './util';
import { useUsers } from 'utils/user';

export default function ProjectList(props: { setProjectModalOpen: (val: boolean) => void }) {
  const { open } = useProjectModal()
  // 存储负责人列表
  // const [users, setUsers] = useState([]);
  // 搜索
  // const [, setParam] = useState({
  //   name: '',
  //   personId: ''
  // });
  const [param, setParam] = useUrlQueryParam(['name', 'personId']);
  const projectParam = useMemo(
    () => ({ ...param, personId: Number(param.personId) || undefined }),
    [param]
  );
  // const [isLoading, setLoading] = useState(false);
  // const [error, setError] = useState<null | Error>(null);
  // const [list, setList] = useState([]);
  // 防抖
  const debouncedParam = useDebounce(projectParam, 300);
  const { isLoading, error, data: list } = useProjects(debouncedParam);
  const { data: users } = useUsers()

  return (
    <Container>
      <Row between={true}>
        <h2>项目列表</h2>
        <Button onClick={open}>创建项目</Button>
      </Row>
      <SearchPanel param={projectParam} setParam={setParam} />
      <ErrorBox error={error} />
      <List loading={isLoading} dataSource={list || []} users={users} setProjectModalOpen={props.setProjectModalOpen} />
    </Container>
  );
}

// 监听当前组件是否有死循环请求
ProjectList.whyDidYouRender = true;

