import styled from '@emotion/styled';
import React from 'react';
import { Kanban, Task } from 'types/kanban';
import { useDeleteTask, useTasks } from 'utils/task';
import { useTaskTypes } from 'utils/task-type';
import bug from 'assets/svgs/bug.svg';
import task from 'assets/svgs/task.svg';
import { Card, Dropdown, Menu, Modal } from 'antd';
import { DeleteOutlined } from '@ant-design/icons'
import { useKanbansQueryKey, useTasksModal, useTasksQueryKey, useTasksSearchParams } from './util';
import { CreatTask } from './create-task';
import { ButtonNoPadding, Row } from 'components/lib';
import { Mark } from 'components/mark';
import { useDeleteKanban } from 'utils/kanban';

const TaskTypeIcon = ({id}: {id: number}) => {
  const {data: taskTypes} = useTaskTypes()
  const name = taskTypes?.find(type => type.id === id)?.name

  if (!name) return null
  return <img src={name === 'task' ? task : bug} alt="" />
}

const TaskCardItem = ({tasks}: {tasks: Task[] | undefined}) => {
  const { startEdit } = useTasksModal()
  const { name: keyword } = useTasksSearchParams()
  const { mutateAsync: delTask } = useDeleteTask(useTasksQueryKey())
  const deleteTask = async (id: number, name: string) => {
    Modal.confirm({
      okText: '删除',
      cancelText: '取消',
      title: '删除',
      content: `是否要删除【${name}】任务`,
      onOk: async () => {
        await delTask({ id })
      }
    })
  }

  return <>
    {
      tasks?.map(t => (<div style={{position: 'relative'}} key={t.id}>
        <Card onClick={() => startEdit(t.id)} 
          style={{marginBottom: '0.5rem', borderRadius: '6px', cursor: 'pointer'}} >
          <div>
            <Mark keyword={keyword} name={t.name} />
          </div>
          <TaskTypeIcon id={t.typeId} />
        </Card>
        <DeleteIcon onClickCapture={() => deleteTask(t.id, t.name)}>
          <DeleteOutlined />
        </DeleteIcon>
      </div>))
    }
  </>
}

export const KanbanColumn = ({kanban}: {kanban: Kanban}) => {
  // const parmas = useTasksSearchParams()
  const {data: allTasks} = useTasks(useTasksSearchParams())
  const tasks = allTasks?.filter(t => t.kanbanId === kanban.id)
  const { mutateAsync: delKanban } = useDeleteKanban(useKanbansQueryKey())


  const deleteKanban = (id: number, name?: string) => {
    Modal.confirm({
      okText: '删除',
      cancelText: '取消',
      title: '删除',
      content: `是否要删除【${name}】看板`,
      onOk: async () => {
        await delKanban({id})
      }
    })
  }

  return <TaskContainer>
    <Row between={true} marginBottom={1}>
      <h3>{kanban.name}</h3>
      <Dropdown overlay={<Menu>
        <Menu.Item onClick={() => deleteKanban(kanban.id, kanban.name)}>删除</Menu.Item>
      </Menu>}>
        <ButtonNoPadding type={'link'} style={{fontSize: '16px'}}>...</ButtonNoPadding>
      </Dropdown>
    </Row>
    <TasksContainer>
      <TaskCardItem tasks={tasks} />
      <CreatTask kanbanId={kanban.id}></CreatTask>
    </TasksContainer>
  </TaskContainer>
}


export const TaskContainer = styled.div`
  display: flex;
  flex-direction: column;
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 1.5rem;
`

const TasksContainer = styled.div`
  overflow: scroll;
  flex: 1;
  ::-webkit-scrollbar {
    display: none;
  }
`

const DeleteIcon = styled.div`
  position: absolute;
  right: 5px;
  bottom: 5px;
  cursor: pointer;
`