import React from "react";
import { Raw } from "types/util";
import { Select } from "antd";

type SelectProps = React.ComponentProps<typeof Select>;

// 自定义类型同时，还要保留原来组件的参数, 继承自SelectProps，除了'value' | 'options' | 'onChange' 三项外
interface IdSelectProps extends Omit<SelectProps, 'value' | 'options' | 'onChange'> {
  value?: Raw | undefined,
  onChange?: (value?: number) => void,
  defaultValueName?: string,
  options?: { name: string, id: number }[]
}

export default function IdSelect(params: IdSelectProps) {
  const { value, onChange, defaultValueName, options, ...restProps } = params;
  return (
    <Select value={ toNumber(value) } onChange={ val => onChange?.( toNumber(val) || undefined) } {...restProps}>
      { defaultValueName ? <Select.Option value={0} >{defaultValueName}</Select.Option> : null }
      {
        options?.map(o => {
          return <Select.Option key={ o.id } value={o.id} >{o.name}</Select.Option>;
        })
      }
    </Select>
  );
}

// 转化为数字
const toNumber = (value: unknown) => isNaN(Number(value)) ? 0 : Number(value);