import React from "react";
import { Form, Input } from "antd";
import { useAuth } from "context/auth-context";
import { LongButton } from ".";

export default function AuthRegister() {
  const { register } = useAuth();

  const handleSubmit = ({ cpassword, ...values }: { username: string, password: string, cpassword: string }) => {
    if (cpassword !== values.password) {
      throw new Error('请确认两次密码相同');
    }
    register(values);
  }

  return <Form onFinish={ handleSubmit }>
    <Form.Item
      name={'username'}
      rules={ [{ required: true, message: '请填写用户名' }] }>
      <Input type={'text'} placeholder={'用户名'}></Input>
    </Form.Item>
    <Form.Item
      name={'password'}
      rules={ [{ required: true, message: '请填写密码' }] }>
      <Input type={'password'} placeholder={'密码'} id={'password'}></Input>
    </Form.Item>
    <Form.Item
      name={'cpassword'}
      rules={ [{ required: true, message: '请确认密码' }] }>
      <Input type={'password'} placeholder={'确认密码'} id={'cpassword'}></Input>
    </Form.Item>
    <Form.Item>
      <LongButton htmlType={'submit'} type={'primary'}>注册</LongButton>
    </Form.Item>
  </Form>
}
