import { Button, Input } from 'antd'
import { Row } from 'components/lib'
import TaskTypeSelect from 'components/task-type-select'
import UserSelect from 'components/user-select'
import React from 'react'
import { useSetUrlSearchParam } from 'utils/url'
import { useTasksSearchParams } from './util'


export const SearchPanel = () => {
  const searchParams = useTasksSearchParams()
  const setUrlParmas = useSetUrlSearchParam()

  const reset = () => {
    setUrlParmas({
      name: undefined,
      typeId: undefined,
      processorId: undefined,
      tagId: undefined
    })
  }
  
  return <Row marginBottom={4} gap={true}>
    <Input style={{width: '200px'}} placeholder={'任务名'} value={searchParams.name} onChange={evt => setUrlParmas({
      name: evt.target.value
    })} />
    <UserSelect defaultValueName={"经办人"} value={searchParams.processorId} onChange={val => setUrlParmas({
      processorId: val
    })}></UserSelect>
    <TaskTypeSelect defaultValueName={"类型"} value={searchParams.typeId} onChange={val => setUrlParmas({typeId: val})}></TaskTypeSelect>
    <Button onClick={reset}>清除筛选器</Button>
  </Row>
}