import styled from '@emotion/styled';
import { Spin } from 'antd';
import { ScreenContainer } from 'components/lib';
import React from "react";
import { useDocumentTitle } from 'utils';
import { useKanbans } from 'utils/kanban';
import { useTasks } from 'utils/task';
import { CreateKanban } from './create-kanban';
import { KanbanColumn } from './kanban-column';
import { SearchPanel } from './search-panel';
import { TaskModal } from './task-modal';
import { useKanbanSearchParams, useProjectInUrl, useTasksSearchParams } from './util';

export const KanbanScreen = () => {
  // 更新标题
  useDocumentTitle("看板列表")
  const { data: kanbans } = useKanbans(useKanbanSearchParams())
  const { data: currentProject, isLoading: isKanbanLoading } = useProjectInUrl()
  const { isLoading: isTaskLoading } = useTasks(useTasksSearchParams())
  const isLoading = isTaskLoading || isKanbanLoading
  return <ScreenContainer>
    <h1>{ currentProject?.name }看板</h1>
    <SearchPanel></SearchPanel>
    {
      isLoading ? <Spin size={'large'} /> : <ColumnContainer>
      {
        kanbans?.map(k => (<KanbanColumn key={k.id} kanban={k}></KanbanColumn>))
      }
      <CreateKanban></CreateKanban>
    </ColumnContainer>
    }
    {/* 编辑任务弹窗 */}
    <TaskModal/>
  </ScreenContainer>
}


export const ColumnContainer = styled.div`
  display: flex;
  justify-content: flex-start;
  overflow-x: scroll;
  flex: 1;
`