import React, { useState } from 'react';
import { useAuth } from 'context/auth-context';
import { Row } from 'components/lib';
import styled from '@emotion/styled';
import ProjectList from './project-list';
import { ReactComponent as SoftwareLogo } from 'assets/svgs/software-logo.svg';
import { Button, Dropdown, Menu } from 'antd';
import { Route, Routes } from 'react-router';
import { BrowserRouter as Router } from 'react-router-dom';
import { ProjectScreen } from './project';
import { resetRoute } from 'utils';
import { ProjectPopover } from 'components/project-popover';
import { ProjectModal } from './project-list/project-modal';
import { UserPopover } from 'components/user-popover';

export const AuthApp = () => {
  const { logout, user } = useAuth();
  const [, setProjectModalOpen] = useState(false);
  return (
    <Container>
      <Router>
        <Header between={true}>
          <HeaderLeft gap={true}>
            <Button type={'link'} onClick={resetRoute}>
              <SoftwareLogo width={'18rem'} color={'rgb(38, 132, 255)'} />
            </Button>
            {/* <h3>项目</h3> */}
            <ProjectPopover></ProjectPopover>
            <UserPopover />
          </HeaderLeft>
          <HeaderRight>
            <Dropdown overlay={<Menu>
              <Menu.Item key={'logout'}>
                <Button type={'link'} onClick={logout}>登出</Button>
              </Menu.Item>
            </Menu>}>
              <Button type={'link'} onClick={e => e.preventDefault()}>
                Hi，{ user?.name }
              </Button>
            </Dropdown>
          </HeaderRight>
        </Header>
        <Main>
          {/* <ProjectList></ProjectList> */}
          <Routes>
            <Route path={'/projects'} element={<ProjectList setProjectModalOpen={setProjectModalOpen} />}></Route>
            <Route path={'/projects/:projectId/*'} element={<ProjectScreen/>}></Route>
            {/* 默认路由：上面路由匹配不到即默认下面的路由 */}
            {/* <Navigate to={'/projects'} /> */}
            <Route index element={<ProjectList setProjectModalOpen={setProjectModalOpen} />}></Route>
          </Routes>
        </Main>
        {/* 新增项目Drawer */}
        <ProjectModal></ProjectModal>
      </Router>
    </Container>
  );
}

const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr;
  height: 100vh;
`;

const Header = styled(Row)`
  padding: 1rem 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .1);
`;

const HeaderLeft = styled(Row)``;

const HeaderRight = styled.div``;

const Main = styled.main`
  display: flex;
  overflow: hidden;
`;