import React, { useEffect } from 'react'
import { Button, Drawer, Form, Input } from 'antd'
import { DrawerProps } from 'antd/es/drawer'
import { ErrorBox } from 'components/lib'
import styled from '@emotion/styled'
import { useAddEpic } from 'utils/epic'
import { useForm } from 'antd/lib/form/Form'
import { useEpicsQueryKey } from './util'
import { useProjectIdInUrl } from 'pages/kanban/util'

export const EpicModal = (props: Pick<DrawerProps, 'visible'> & { onClose: () => void }) => {
  const { isLoading, error, mutateAsync: addEpic } = useAddEpic(useEpicsQueryKey())
  const projectId = useProjectIdInUrl()
  const [form] = useForm()
  const onFinish = async (values: any) => {
    await addEpic({...values, projectId})
    props.onClose()
  }

  useEffect(() => {
    form.resetFields()
  }, [form, props.visible])

  return <Drawer onClose={props.onClose} forceRender={true} destroyOnClose={true} width={'100%'} visible={props.visible}>
    <Container>
      <h1>创建任务组</h1>
      <ErrorBox error={error}></ErrorBox>
      <Form form={ form } layout={"vertical"} style={{width: '40rem'}} onFinish={onFinish}>
        <Form.Item label={"名称"} name={"name"} rules={[ {required: true, message: '请输入任务组名称'} ]}>
          <Input placeholder='请输入任务组名称' />
        </Form.Item>
        <Form.Item>
          <SubmitButton>
            <Button loading={isLoading} style={{width: '100%'}} type={'primary'} htmlType={"submit"}>保存</Button>
          </SubmitButton>
        </Form.Item>
      </Form>
    </Container>
  </Drawer>
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;
`

const SubmitButton = styled.div`
  width: 100%;
`