import React from 'react';
import { Button, Drawer, Form, Input, Spin } from 'antd';
import { useProjectModal, useProjectQueryKey } from './util';
import styled from '@emotion/styled';
import UserSelect from 'components/user-select';
import { useForm } from 'antd/lib/form/Form';
import { useAddProject, useEditProject } from 'utils/project';
import { ErrorBox } from 'components/lib';
import { useEffect } from 'react';

export const ProjectModal = () => {
  const { close, projectCreate, editingProjectId, editingProject, isLoading } = useProjectModal()
  const [ form ] = useForm()

  const useMutateProject = editingProjectId ? useEditProject : useAddProject
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectQueryKey())

  const title = editingProjectId ? '编辑项目' : '新增项目'

  const onFinish = (values: any) => {
    mutateAsync({...editingProject, ...values}).then(() => {
      closeModal()
    })
  }

  const closeModal = () => {
    form.resetFields() // 重置表单
    close()
  }

  // editingProject 改变的时候需要重置表单，即回填
  useEffect(() => {
    form.setFieldsValue(editingProject)
  }, [editingProject, form])

  return (
    <Drawer onClose={closeModal} width={'100%'} visible={projectCreate} getContainer={false}>
      <Container>
      { 
        isLoading ? (<Spin size={"large"}></Spin>) : (<>
        <h1>{ title }</h1>
        <ErrorBox error={error}></ErrorBox>
        <Form form={ form } layout={"vertical"} style={{width: '40rem'}} onFinish={onFinish}>
          <Form.Item label={"名称"} name={"name"} rules={[ {required: true, message: '请输入项目名称'} ]}>
            <Input placeholder='请输入项目名称' />
          </Form.Item>
          <Form.Item label={"部门"} name={"organization"} rules={[ {required: true, message: '请输入部门名称'} ]}>
            <Input placeholder='请输入部门名称' />
          </Form.Item>
          <Form.Item label={"负责人"} name={"personId"}>
            <UserSelect defaultValueName={'负责人'}></UserSelect>
          </Form.Item>
          <Form.Item>
            <SubmitButton>
              <Button loading={mutateLoading} style={{width: '100%'}} type={'primary'} htmlType={"submit"}>提交</Button>
            </SubmitButton>
          </Form.Item>
        </Form>
        </>
      )}
      </Container>
    </Drawer>
  );
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;
`

const SubmitButton = styled.div`
  width: 100%;
`