import qs from 'qs';
import * as auth from 'auth-provider';
import { useAuth } from 'context/auth-context';

const baseUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';

interface Config extends RequestInit {
  data?: object,
  token?: string
}

export const http = (url: string, { data, token, headers, ...customConfig }: Config = {}) => {
  const config = {
    method: 'GET',
    headers: {
      Authorization: token ? `Bearer ${token}` : '',
      "Content-type": data ? 'application/json' : ''
    },
    ...customConfig
  }
  if (config.method.toUpperCase() === 'GET') {
    url += `?${qs.stringify(data)}`;
  } else {
    config.body = JSON.stringify(data || {});
  }

  // axios 的表现是当接口出现401等错误状态的时候会抛出异常，而fetch不会，需要借助Promise.reject()
  return window.fetch(`${baseUrl}/${url}`, config).then(async response => {
    if (response.status === 401) {
      await auth.logout();
      window.location.reload(); // 重新刷新页面
      return Promise.reject({ message: '请重新登录' });
    }

    const data = await response.json();
    if (response.ok) {
      return data;
    } else {
      return Promise.reject(data);
    }
  });
};

export const useHttp = () => {
  const { user } = useAuth();
  // Parameters: 获取函数的参数类型
  return (...[url, config]: Parameters<typeof http>) => http(url, { ...config, token: user?.token });
}
