import { useMemo } from 'react';
import { useProject } from 'utils/project';
import { useUrlQueryParam } from 'utils/url'

// 项目列表搜索的参数
export const useProjectsSearchParams = () => {
  const [param, setParam] = useUrlQueryParam(["name", "personId"]);

  return [
    useMemo(
      () => ({...param, personId: Number(param.personId) || undefined}),
      [param]
    ),
    setParam
  ] as const
};

export const useProjectQueryKey = () => {
  const [param] = useProjectsSearchParams()
  return ['projects', param]
}

// 用query管理modal的状态
export const useProjectModal = () => {
  const [{projectCreate}, setProjectCreate] = useUrlQueryParam(['projectCreate'])

  const [{editingProjectId}, setEditingProject] = useUrlQueryParam(['editingProjectId'])

  const { data: editingProject, isLoading } = useProject(Number(editingProjectId))

  const open = () => setProjectCreate({ projectCreate: true })
  const close = () => {
    projectCreate === 'true' && setProjectCreate({ projectCreate: undefined })
    Boolean(editingProjectId) && setEditingProject({ editingProjectId: undefined })
  }
  const startEdit = (id: number) => setEditingProject({ editingProjectId: id })
  // console.log(editingProjectId, '======projectCreate=======', projectCreate);
  return {
    projectCreate: projectCreate === 'true' || Boolean(editingProjectId),
    open,
    close,
    startEdit,
    editingProject, // 拿到详情接口的值
    isLoading,
    editingProjectId
  }
}