import React from 'react';
import './App.css';
import UnAuthApp from 'pages/auth-app/index';
import { AuthApp } from 'pages/authenticated-app';
import { useAuth } from 'context/auth-context';
// const UnAuthApp = React.lazy(() => import('pages/auth-app/index'));

function App() {
  const { user } = useAuth();
  return (
    <div className="App">
      {
        user?.token ? (<AuthApp />) : (<UnAuthApp />)
      }
    </div>
  );
}

export default App;
