import React from "react";
import { Form, Input } from "antd";
import UserSelect from "components/user-select";
import { Project } from 'types/project';

interface SearchProps {
  param: Partial<Pick<Project, 'name' | 'personId'>>,
  // param: {
  //   name: string,
  //   personId: string
  // },
  setParam: (param: SearchProps['param']) => void
}

export const SearchPanel = ({ param, setParam }: SearchProps) => {
  return (
    <Form layout={'inline'} style={{ marginBottom: "2rem" }}>
      <Form.Item>
        <Input placeholder={'项目名'} type={'text'}
          value={param.name}
          onChange={evt => {
            setParam({
              ...param,
              name: evt.target.value
            });
          }}></Input>
      </Form.Item>
      <Form.Item>
        <UserSelect
          defaultValueName="负责人"
          value={param.personId}
          onChange={
            val => {
              setParam({
                ...param,
                personId: val
              })
            }
          }
          ></UserSelect>
        {/* <Select value={param.personId}
          onChange={ val => {
            setParam({
              ...param,
              personId: val
            })
          } }>
          <Select.Option value={""}>负责人</Select.Option>
          {
            users.map(u => {
              return <Select.Option key={u.id} value={String(u.id)}>{u.name}</Select.Option>
            })
          }
        </Select> */}
      </Form.Item>
    </Form>
  );
};