import React from "react";
import { KanbanScreen } from "pages/kanban";
import { EpicScreen } from "pages/epic";
import { Routes, Route, useLocation } from 'react-router';
import { Link } from 'react-router-dom';
import styled from '@emotion/styled';
import { Menu } from 'antd';

const useRouteType = () => {
  const units = useLocation().pathname.split('/')
  console.log('units', units)
  const keyList = ['kanban', 'epic']
  const key = units[units.length - 1]
  return keyList.includes(key) ? key : 'kanban'
}

export const ProjectScreen = () => {
  const routeKey = useRouteType()
  return (
    <Container>
      <Aside>
        <Menu mode={'inline'} selectedKeys={[routeKey]}>
          <Menu.Item key={'kanban'}>
            <Link to={'kanban'}>看板</Link>
          </Menu.Item>
          <Menu.Item key={'epic'}>
            <Link to={'epic'}>任务组</Link>
          </Menu.Item>
        </Menu>
      </Aside>
      <Main>
        <Routes>
          <Route path={'/kanban'} element={<KanbanScreen/>}></Route>
          <Route path={'/epic'} element={<EpicScreen/>}></Route>
          {/* 默认路由：上面路由匹配不到即默认下面的路由 */}
          {/* <Navigate to={window.location.pathname + '/kanban'} /> 6.0版本之前 */}
          <Route index element={<KanbanScreen/>}></Route>
        </Routes>
      </Main>
    </Container>
  );
}


const Aside = styled.aside`
  background-color: rgb(244,245,247);
  display: flex;
`
const Main = styled.div`
  box-shadow: -5px 0 5px -5px rgba(0,0,0,.1);
  display: flex;
  overflow: hidden;
`

const Container = styled.div`
  display: grid;
  grid-template-columns: 16rem 1fr;
  width: 100%;
`