import React from 'react';
import { Divider, List, Popover, Typography } from 'antd';
import styled from '@emotion/styled';
import { useProjects } from 'utils/project';
import { ButtonNoPadding } from './lib';
import { useProjectModal } from 'pages/project-list/util';

export const ProjectPopover = () => {
  const { open } = useProjectModal()
  const { data: projects, refetch } = useProjects();
  const pinnedProjects = projects?.filter(p => p.pin);
  const content = <ContentContiner>
    <Typography.Text type={"secondary"}>收藏项目</Typography.Text>
    <List>
      {
        pinnedProjects?.map(p => <List.Item key={p.name}>
          <List.Item.Meta title={p.name}></List.Item.Meta>
        </List.Item>)
      }
    </List>
    <Divider/>
    <ButtonNoPadding type={"link"} onClick={() => open()}>创建项目</ButtonNoPadding>
  </ContentContiner>

  return <Popover onVisibleChange={() => refetch()} content={content}>
  <span>项目</span>
</Popover>;
};

const ContentContiner = styled.div`
  width: 20rem;
  padding: 0;
`;