import React from 'react';

// 查询关键字变色
export const Mark = ({name, keyword}: {name: string | undefined, keyword: string}) => {
  if (!name) return <></>
  const str = name.split(keyword)
  return <>
    {
      str.map((s, index) => {
        return <span key={`id_${index}`}>
          {s}
          { index === str.length - 1 ? null : (<span style={{color: '#257AFD'}}>{keyword}</span>) }
        </span>
      })
    }
  </>
}