import styled from '@emotion/styled';
import { List } from 'antd';
import { ButtonNoPadding, Row, ScreenContainer } from 'components/lib';
import dayjs from 'dayjs';
import { useProjectInUrl } from 'pages/kanban/util';
import React, { useState } from "react";
import { Link } from 'react-router-dom';
import { useEpics } from 'utils/epic';
import { useTasks } from 'utils/task';
import { EpicModal } from './epic-modal';
import { useEpicSearchParams } from './util';

export const EpicScreen = () => {
  const { data: currentProject } = useProjectInUrl()
  const { data: epics } = useEpics(useEpicSearchParams())
  const { data: tasks } = useTasks({ projectId: currentProject?.id })
  const [openEpic, setOpenEpic] = useState(false)

  return <ScreenContainer>
    <Row between={true}>
      <h1>{currentProject?.name}任务组</h1>
      <ButtonNoPadding style={{ padding: '0 8px' }} onClick={() => setOpenEpic(true)}>创建任务组</ButtonNoPadding>
    </Row>
    <Container>
      <List dataSource={epics} itemLayout={'vertical'} renderItem={
        epic => <List.Item>
          <List.Item.Meta 
            title={<Row between={true}>
              <span>{epic.name}</span>
              <ButtonNoPadding type={'link'}>删除</ButtonNoPadding>
            </Row>}
            description={
              <div>
                <div>开始时间：{dayjs(epic.start).format('YYYY-MM-DD HH:mm:ss')}</div>
                <div>结束时间：{dayjs(epic.end).format('YYYY-MM-DD HH:mm:ss')}</div>
              </div>
            }
          />
          <TaskContainer>
            {
              tasks?.filter(task => task.epicId === epic.id).map(t => 
                <Link to={`/projects/${t.kanbanId}/kanban`} key={t.id}>{t.name}</Link>)
            }
          </TaskContainer>
        </List.Item>
      } />
    </Container>
    <EpicModal onClose={() => setOpenEpic(false)} visible={openEpic}  />
  </ScreenContainer>
}

const TaskContainer = styled.div`
  display: flex;
  flex-direction: column;
  line-height: 2.8rem;
`

const Container = styled.div`
  overflow-y: scroll;
  ::-webkit-scrollbar {
    display: none;
  }
`