import React, { useEffect, useState } from 'react';
import { Card, Input } from 'antd';
import styled from '@emotion/styled';
import { useAddTask } from 'utils/task';
import { useProjectIdInUrl, useTasksQueryKey } from './util';

export const CreatTask = ({kanbanId}: { kanbanId: number }) => {
  const [name, setName] = useState('')
  const [inputMode, setInputMode] = useState(false) // 切换输入框
  const projectId = useProjectIdInUrl()
  const { mutateAsync: addTask } = useAddTask(useTasksQueryKey())
  const toggle = () => {
    setInputMode(mode => !mode)
  }

  const submitTask = async () => {
    await addTask({ name, projectId, kanbanId })
    setName('')
  }

  // 监控mode 为false的时候，清空name
  useEffect(() => {
    if (!inputMode) setName('')
  }, [inputMode])

  if (!inputMode) return <Card style={{marginBottom: '0.5rem', borderRadius: '6px'}}>
    <TaskDiv onClick={toggle}>+创建事务</TaskDiv>
  </Card>
    
  return <Card style={{marginBottom: '0.5rem', borderRadius: '6px'}}>
    <Input size={'large'} placeholder={'输入任务名称'} autoFocus={true} value={name} onChange={
      evt => setName(evt.target.value)
    } onBlur={toggle} onPressEnter={submitTask} />
  </Card>
}


const TaskDiv = styled.div`
  cursor: pointer;
`