import React from 'react';
import { List, Popover, Typography } from 'antd';
import styled from '@emotion/styled';
import { useUsers } from 'utils/user';

export const UserPopover = () => {
  const { data: users, refetch } = useUsers();
  const content = <ContentContiner>
    <Typography.Text type={"secondary"}>用户列表</Typography.Text>
    <List>
      {
        users?.map(u => <List.Item key={u.id}>
          <List.Item.Meta title={u.name}></List.Item.Meta>
        </List.Item>)
      }
    </List>
  </ContentContiner>

  return <Popover onVisibleChange={() => refetch()} content={content}>
  <span>组员</span>
</Popover>;
};

const ContentContiner = styled.div`
  width: 20rem;
  padding: 0;
`;