import React from "react";
import { User } from "types/auth";
import { Dropdown, Menu, Modal, Table, TableProps } from "antd";
import dayjs from 'dayjs';
// react-router和react-router-dom的关系类似于react和react-dom/react-native的关系
import { Link } from 'react-router-dom';
import PinRate from "components/pin";
import { useDeleteProject, useEditProject } from "utils/project";
import { ButtonNoPadding } from 'components/lib';
import { useProjectModal, useProjectQueryKey } from './util';
import { Project } from 'types/project';


interface ListProps extends TableProps<Project> {
  users: User[] | undefined
  refresh?: () => void,
  setProjectModalOpen: (isOpen: boolean) => void
}

// props 是Table组件的属性
export const List = ({users, ...props }: ListProps) => {
  const { mutate } = useEditProject(useProjectQueryKey());
  const pinProject = (id: number) => (pin: boolean) => mutate({id, pin});
  const columns = [
    {
      title: <PinRate checked={true} disabled={true}></PinRate>,
      render(value: unknown, project: Project) {
        return <PinRate checked={project.pin} onCheckedChange={ pinProject(project.id) }></PinRate>
      }
    },
    {
      title: '名称',
      // dataIndex: "name",
      sorter: (a: Project, b: Project) => a.name.localeCompare(b.name),
      render(value: unknown, project: Project) {
        return <Link to={`projects/${project.id}`}>{project.name}</Link>
      }
    },
    {
      title: '部门',
      dataIndex: 'organization'
    },
    {
      title: '负责人',
      render(value: unknown, project: Project) {
        return (
          <span>{users?.find(user => user.id === project.personId)?.name || "未知"}</span>
        );
      }
    },
    {
      title: '创建时间',
      render(value: unknown, project: Project) {
        return (
          <span>{ project.created ? dayjs(project.created).format('YYYY-MM-DD HH:mm:ss') : '--' }</span>
        );
      }
    },
    {
      render(value: unknown, project: Project) {
        return <More project={project} />
      }
    }
  ]

  return (
    <Table
      rowKey={'id'}
      pagination={false}
      columns={columns}
      {...props}></Table>
  );
};

const More = (props: { project: Project}) => {
  const { startEdit } = useProjectModal()
  const { mutate } = useDeleteProject(useProjectQueryKey())
  const deleteItem = (id: number, name: string | undefined) => {
    Modal.confirm({
      title: '确定要删除该项目?',
      content: `删除${name}项目`,
      onOk: () => {
        mutate({ id })
      }
    })
  }
  return <Dropdown overlay={
    <Menu>
      <Menu.Item key={"edit"}>
        <ButtonNoPadding type={"link"} onClick={() => startEdit(props.project.id)}>编辑</ButtonNoPadding>
      </Menu.Item>
      <Menu.Item onClick={() => deleteItem(props.project.id, props.project.name)} key={"del"}>删除</Menu.Item>
    </Menu>
  }>
    <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
  </Dropdown>
}