import React, { useState } from 'react'
import { Input } from 'antd'
import { TaskContainer } from './kanban-column'
import { useKanbansQueryKey, useProjectIdInUrl } from './util'
import { useAddKanban } from 'utils/kanban'

export const CreateKanban = () => {
  const [name, setName] = useState('')
  const projectId = useProjectIdInUrl()
  const { mutateAsync } = useAddKanban(useKanbansQueryKey())

  const submitKanbans = async () => {
    await mutateAsync({ name, projectId })
    setName('')
  }
  
  return <TaskContainer>
    <Input size={'large'} placeholder={'请输入看板名称'} value={name} onPressEnter={submitKanbans} onChange={ evt => setName(evt.target.value) }></Input>
  </TaskContainer>
}